$(document).ready(function () {

    //Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__desktop').toggleClass('header__desktop--active')
        $('body').toggleClass('fixed-body')
    })

    $('.header__list li a').click(function () {
        $('.burger').removeClass('burger--active')
        $('.header__desktop').removeClass('header__desktop--active')
        $('body').removeClass('fixed-body')
    })

    //Select
    $(".select-show").click(function(e) {
        var $select = $(this).closest('.select');

        $('.select').not($select).removeClass('select--active');

        $select.toggleClass('select--active');
    });

    $(".select-item").click(function() {
        var e = $(this).html();
        var $select = $(this).closest('.select');

        $select.addClass('select--color');
        $select.find(".select-current").html(e);
        $select.removeClass("select--active");
        $(this).siblings(".select-item").removeClass("select-item--active");
        $(this).addClass("select-item--active");
    });

    document.addEventListener('click', function(e) {
        if (!e.target.closest('.select')) {
            document.querySelectorAll('.select.select--active').forEach(function(el){
                el.classList.remove('select--active');
            });
        }
    }, true);

    //Calendar
    const airDatepicker = new AirDatepicker('.calendar-input', {
        minDate: new Date()
    });

// Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")
        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
        initCalendarInModal(modalId)
    }

    function initCalendarInModal(modalId) {
        const calendarInput = $(`#${modalId} .calendar-input`)[0];

        if (calendarInput && calendarInput._airDatepicker) {
            calendarInput._airDatepicker.update({
                visible: true,
                minDate: new Date()
            });
        }
        else if (calendarInput) {
            new AirDatepicker('.calendar-input', {
                container: `#${modalId} .calendar-group`,
                minDate: new Date(),
                onShow: function(isShow) {
                    if (isShow && this.$datepicker) {
                        this.$datepicker.css({
                            'z-index': 9999,
                            'position': 'fixed'
                        });
                    }
                }
            });
        }
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })

    function closeModal() {
        $('.modal--active video').each(function () {
            this.pause();
            this.currentTime = 0;
        });

        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }


})